body {
  font-family: sans-serif;
  margin: 0;
}
#theme-toggle {
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(26px);
  background-color: black;
}

#toggle-label {
  margin-left: 10px;
  font-weight: bold;
  color: black;
}

#content {
  background-color: white;
  color: black;
  padding: 20px;
  font-family: sans-serif;
}

.dark-mode #content {
  background-color: black;
  color: white;
}
